import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { ContactManagerJourneyConfigurationService } from './contact-manager-journey-config.service';
/**
 * Service provides the journey configuration
 *
 * @usageNotes
 *
 * ### Ensure the ContactManagerJourneyModule is imported to your component module
 *
 * ```ts
 * @NgModule({
 *   ...
 *   imports: [
 *     ...
 *     ContactManagerJourneyModule,
 *   ],
 * })
 * export class ContactJourneyBundleModule {}
 * ```
 *
 * ### Inject this service into your component
 *
 * ```ts
 * @Component({
 *   ...
 *   providers: [ContactManagerJourneyPreferencesService],
 * })
 *  export class ContactManagerJourneyModule {
 * ```
 */
@Injectable()
export class ContactManagerJourneyPreferencesService {
  private readonly newContactSubject$ = new BehaviorSubject<boolean>(this.config.newContact);
  private readonly pageSizeSubject$ = new BehaviorSubject<number>(this.config.pageSize);
  private readonly maxNavPagesSubject$ = new BehaviorSubject<number>(this.config.maxNavPages);
  private readonly paginationTypeSubject$ = new BehaviorSubject<string>(this.config.paginationType);
  private readonly createContactEmailSubject$ = new BehaviorSubject<boolean>(this.config.createContactEmail);
  private readonly createContactIBANSubject$ = new BehaviorSubject<boolean>(this.config.createContactIBAN);
  private readonly createContactAccountSubject$ = new BehaviorSubject<boolean>(this.config.createContactAccount);
  private readonly createContactPhoneSubject$ = new BehaviorSubject<boolean>(this.config.createContactPhone);
  private readonly notificationDismissTimeSubject$ = new BehaviorSubject<number>(this.config.notificationDismissTime);

  readonly newContact$ = this.newContactSubject$.asObservable();
  readonly pageSize$ = this.pageSizeSubject$.asObservable();
  readonly maxNavPages$ = this.maxNavPagesSubject$.asObservable();
  readonly paginationType$ = this.paginationTypeSubject$.asObservable();
  readonly createContactEmail$ = this.createContactEmailSubject$.asObservable();
  readonly createContactIBAN$ = this.createContactIBANSubject$.asObservable();
  readonly createContactAccount$ = this.createContactAccountSubject$.asObservable();
  readonly createContactPhone$ = this.createContactPhoneSubject$.asObservable();
  readonly notificationDismissTime$ = this.notificationDismissTimeSubject$.asObservable();

  setNewContact(value: boolean) {
    this.newContactSubject$.next(value);
  }

  setPageSize(value: number) {
    this.pageSizeSubject$.next(value);
  }

  setMaxNavPages(value: number) {
    this.maxNavPagesSubject$.next(value);
  }

  setPaginationType(value: string) {
    this.paginationTypeSubject$.next(value);
  }

  setNotificationDismissTime(value: number) {
    this.notificationDismissTimeSubject$.next(value);
  }

  setCreateContactEmail(value: boolean) {
    this.createContactEmailSubject$.next(value);
  }

  setCreateContactIBAN(value: boolean) {
    this.createContactIBANSubject$.next(value);
  }

  setCreateContactAccount(value: boolean) {
    this.createContactAccountSubject$.next(value);
  }

  setCreateContactPhone(value: boolean) {
    this.createContactPhoneSubject$.next(value);
  }

  constructor(private readonly config: ContactManagerJourneyConfigurationService) {}
}
